"use client";

import React, { ReactNode } from "react";
import { motion, Variants } from "framer-motion";

interface AnimatedGroupProps {
  children: ReactNode;
  className?: string;
  variants?: {
    container?: any;
    item?: any;
  };
  delay?: number;
  duration?: number;
  transition?: any;
  initial?: string;
  animate?: string;
  exit?: string;
}

export function AnimatedGroup({
  children,
  className = "",
  variants = {
    container: {
      hidden: { opacity: 0 },
      visible: {
        opacity: 1,
        transition: {
          staggerChildren: 0.1,
        },
      },
    },
    item: {
      hidden: { opacity: 0, y: 10 },
      visible: {
        opacity: 1,
        y: 0,
        transition: {
          type: "spring",
          stiffness: 150,
          damping: 20,
        },
      },
    },
  },
  delay = 0,
  duration,
  transition,
  initial = "hidden",
  animate = "visible",
  exit,
}: AnimatedGroupProps) {
  return (
    <motion.div
      className={className}
      initial={initial}
      animate={animate}
      exit={exit}
      variants={variants.container}
      transition={{
        delayChildren: delay,
        staggerChildren: 0.1,
        duration,
        ...transition,
      }}
    >
      {React.Children.map(children, (child, index) => {
        if (React.isValidElement(child)) {
          return (
            <motion.div key={index} variants={variants.item}>
              {child}
            </motion.div>
          );
        }
        return child;
      })}
    </motion.div>
  );
}
