"use client";

import React, { ReactNode } from "react";
import { motion } from "framer-motion";

interface TextEffectProps {
  children: ReactNode;
  as?: React.ElementType;
  className?: string;
  preset?: "fade-in-blur" | "slide-up" | "slide-down";
  delay?: number;
  duration?: number;
  speedSegment?: number;
  per?: "word" | "character" | "line";
}

export function TextEffect({
  children,
  as: Component = "div",
  className = "",
  preset = "fade-in-blur",
  delay = 0,
  duration = 0.5,
  speedSegment = 0.05,
  per = "word",
}: TextEffectProps) {
  const text = React.Children.toArray(children)[0] as string;

  const getTextSegments = () => {
    if (per === "word") return text.split(" ").map((word) => `${word} `);
    if (per === "character") return text.split("");
    if (per === "line") return text.split("\n");
    return [text];
  };

  const segments = getTextSegments();

  const getPresetAnimation = () => {
    switch (preset) {
      case "fade-in-blur":
        return {
          hidden: { opacity: 0, filter: "blur(8px)" },
          visible: { opacity: 1, filter: "blur(0px)" },
        };
      case "slide-up":
        return {
          hidden: { opacity: 0, y: 20 },
          visible: { opacity: 1, y: 0 },
        };
      case "slide-down":
        return {
          hidden: { opacity: 0, y: -20 },
          visible: { opacity: 1, y: 0 },
        };
      default:
        return {
          hidden: { opacity: 0 },
          visible: { opacity: 1 },
        };
    }
  };

  const animation = getPresetAnimation();

  return (
    <Component className={className}>
      <span className="sr-only">{text}</span>
      <motion.span
        initial="hidden"
        animate="visible"
        aria-hidden
        className="inline-block"
      >
        {segments.map((segment, i) => (
          <motion.span
            key={i}
            className="inline-block"
            variants={animation}
            transition={{
              duration,
              delay: delay + i * speedSegment,
              ease: "easeOut",
            }}
          >
            {segment}
          </motion.span>
        ))}
      </motion.span>
    </Component>
  );
}
